.nav {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    padding: 20px;
    height: 30px;
    z-index: 1;
    justify-content: space-between;
    /*Animation*/
    transition-timing-function: ease-in;
    transition: all 0.5s;
  }
  
  .nav__black {
    background-color: #333;
  }
  
  .nav__logo {
    position: fixed;
    left: 20;
    width: 80px;
    object-fit: contain;
  }
  
  .nav__avtar {
    width: 30px;
    position: fixed;
    right: 20px;
    object-fit: contain;
  }